.services_container {
    grid-template-columns: repeat(3, 270px);
    justify-content: center;
    column-gap: 2rem;
}

.services_content {
position: relative;
border: 0.1px solid rgb(10, 46, 54);
background-color: var(--container-color);
background-image: url('');
padding: 6rem 0 2rem 2.5rem;
border-radius: 25px;
box-shadow: 2px 2px 2px 0px rgba(0, 0, 0, 0.1);
/* background-color: rgb(249, 249, 254); */
}

.services_content:hover{
    animation: moveUp 1s ease-in;
}

@keyframes moveUp {
    0% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-15px);
    }
    100% {
        transform: translateY(0);
    }
}

.services_icon {
    display: block;
    font-size: 3rem;
    /* color: var(--title-color); */
    -webkit-text-stroke: 0.8px #0d0d0d;
    margin-bottom: var(--mb-1);
    color:white;
}

.services_title {
    font-size: var(--h3-font-size);
    margin-bottom: var(--mb-1);
    font-weight: var(--font-medium);
    color:  #e8e6e5;
    -webkit-text-stroke: 0.8px #051d0e;
}

.services_button {
    color: var(--title-color);
    font-size: var(--small-font-size);
    display: inline-flex;
    align-items: center;
    column-gap: 0.25rem;
    cursor: pointer;
}

.services_button-icon {
    font-size: 1rem;
    transition: 0.3s;
}

.services_button:hover {
    transform : translateX(0.5rem);
    transition-duration: 0.5s;
 
}

.services_modal {
    position: fixed;
    top: 0%;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: var(--z-modal);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
    opacity: 0;
    visibility: hidden;
    transition: 0.5s;

}

.services_modal-content {
    width: 500px;
    position: relative;
    background-color: var(--container-color);
    padding: 4.5rem 2.5rem 2.5rem;
    border-radius: 1.5rem;

}

.services_modal-close {
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    font-size: 1.5rem;
    color: var(--title-color);
    cursor: pointer;
    color: red;
}

.services_modal-title,
.services_modal-description {
    text-align: center;
    
}


.services_modal-title {
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1);
   
}

.services_modal-description {
    font-size: var(--small-font-size);
    /* padding: 0 3.5rem; */
    padding: auto;
    margin-bottom: var(--mb-2);

}

.services_modal-services {
    row-gap: 0.75rem;
}

.services_modal-services {
    display: flex;
    align-items: center;
    column-gap: 0.5rem;
    color: royalblue;
   
}

.services_modal-icon {
    color: rgb(5, 11, 5);
    font-size:1.2rem;
}

.services_modal-info {
    font-size: 0.8rem;;
}

/*------ Active Modal -----*/
.active-modal {
    opacity: 1;
    visibility: visible;
}

.services_card-name:hover {
    transform: scale(1.05);
}

/* .services_modal {
    display: none;
} */

/* ==========break points========== */

@media screen and (max-width:992px) {
  .services_container {
    grid-template-columns: repeat(3, 218px);
  }  
  
  }
  
  @media screen and (max-width: 768px) {
    .services_container {
        grid-template-columns: repeat(2, 1fr);
      }  

      .services_content {
        padding:3.5rem 0.5rem 1.25rem 1.5rem ;
      }

      .services_icon {
        font-size: 1.5rem;
      }
  }
  
  @media screen and (max-width: 576px) {
    .services_modal-content {
        padding: 4.5rem 1.5rem 2.5rem;
    }

    .services_modal-description {
    padding: 0;

    } 
  }
  
  @media screen and (max-width: 350px) {
   .services_container {
    grid-template-columns: max-content;
   }

   .services_content {
    padding-right: 6rem;
   }
  }
  