 .timeline,.timeline2 {
     position: relative;
     max-width: 1000px;
     margin: auto;
 }

 .qualification-container {
     padding: 0px 35px;
     position: relative;
     width: 50%;
     animation: movedown 1s linear forwards;
     opacity: 0;
 }

 @keyframes movedown {
     0% {
         opacity: 1;
         transform: translateY(-30px);
     }

     100% {
         opacity: 1;
         transform: translateY(0px);
     }
 }

 .qualification-container:nth-child(1) {
     animation-delay: 0s;
 }

 .qualification-container:nth-child(2) {
     animation-delay: 1.6s;
 }

 .qualification-container:nth-child(3) {
     animation-delay: 3.2s;
 }

 .qualification-container:nth-child(4) {
     animation-delay: 4.8s;
 }

 .text-box {
     padding: 10px 30px;
     background: rgba(10, 99, 169, 0.2);
     position: relative;
     border-radius: 10px;
     font-size: 15px;
     max-width: 100%;
     box-sizing: border-box;
     word-wrap: break-word;
     box-shadow: 2px 2px 2px 0px rgba(0, 0, 0, 0.1);
     margin: 15px;

 }

 .left-container {
     left: 0%
 }

 .right-container {
     left: 50%;
 }

 .qualification-container img {
     position: absolute;
     width: 40px;
     border-radius: 50%;
     right: -20px;
     top: 32px;
     z-index: 5;
     background-color: rgb(81, 81, 86);
     border: 2px solid black    ;
 }

 .right-container img {
     left: -20px;

 }


 .timeline::after {
     content: '';
     position: absolute;
     width: 7px;
     height: 100%;
     background: silver;
     top: 0;
     left: 50%;
     margin-left: -5px;
     z-index: -4;
     animation: moveline 6s linear forwards;
 }

 @keyframes moveline {
     0% {
         height: 0;
     }

     100% {
         height: 100%;
     }

 }

 .timeline2::after {
    content: '';
    position: absolute;
    width: 7px;
    height: 100%;
    background: silver;
    top: 0;
    left: 50%;
    margin-left: -5px;
    z-index: -4;
    animation: moveline 2.5s linear forwards;
}

@keyframes moveline {
    0% {
        height: 0;
    }

    100% {
        height: 100%;
    }

}

 .text-box h4 {
     color: black;
     font-size: 20px;
 }

 .text-box small {
     display: inline-block;
     margin-bottom: 15px;
 }

 .text-box p {
     color:var(--title-color-dark);
     font-size: 14px;
 }

 .left-container-arrow {
     height: 0;
     width: 0;
     position: absolute;
     top: 28px;
     z-index: 1;
     border-top: 15px solid transparent;
     border-bottom: 15px solid transparent;
     border-left: 15px solid #0d0d0d;
     right: -15px;

 }

 .right-container-arrow {
     height: 0;
     width: 0;
     position: absolute;
     top: 28px;
     z-index: 1;
     border-top: 15px solid transparent;
     border-bottom: 15px solid transparent;
     border-right: 15px solid rgba(0, 0, 0, 0.1);
     left: -15px;
 }



 @media screen and (max-width:570px) {

     .timeline {
         margin: 50px auto;
     }

     .timeline::after {
         left: 31px;
     }

     .qualification-container {
         width: 100%;
         padding-left: 80px;
         padding-right: 25px;
     }

     .text-box {
         font-size: 13px;
     }

     .text-box small {
         margin-bottom: 10px;
     }

     .right-container {
         left: 0;
     }

     .left-container img,
     .right-container img {
         left: 10px;
     }

     .left-container-arrow,
     .right-container-arrow {
         border-right: 15px solid #fff;
         border-left: 0;
         left: -15px;
     }

 }

 @media screen and (max-width: 768px) {

     .timeline {
         margin: 50px auto;
     }

     .timeline::after {
         left: 31px;
     }

     .qualification-container {
         width: 100%;
         padding-left: 80px;
         padding-right: 25px;
     }

     .text-box {
         font-size: 13px;
     }

