.contact_container {
    grid-template-columns: repeat(2, max-content);
    justify-content: center;
    column-gap: 6rem;
    padding-bottom: 3rem;
}

.contact_title {
    text-align: center;
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1-5);

}

.contact_info {
display: grid;
row-gap: 1rem;
grid-template-columns: 300px;

}

.contact_card {
    background-color: var(--container-color);
    border: 1px solid rgba(0,0, 0, 0.1);
    padding: 1rem;
    border-radius: 0.75rem;
    text-align: center;
    box-shadow: 2px 2px 2px 0px rgba(0, 0, 0, 0.4);

}

.contact_card-icon {
    font-size: 2rem;
    color: var(--title-color);
    margin-bottom: var(--mb-0-25);
}

.contact_card-title,
.contact_card-data {
    font-size: var(--small-font-size);
}

.contact_card-title {
    font-weight: var(--font-medium);
}

.contact_card-data {
    display: block;
    margin-bottom: var(--mb-0-75);
}

.contact_button {
    color: var(--text-color);
    font-size: var(--small-font-size);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    column-gap: 10.00rem;

}

.contact_button-icon {
    font-size: 1rem;
    transition: 0.3s;
}

.contact_button:hover .contact_button-icon{
    transform: translate(0.45rem);
    color: blue;
}

.contact_form {
    width: 360px;
}

.contact_form-div {
    position: relative;
    margin-bottom: var(--mb-2);
    height: 4rem;
    
}

.contact_form-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid rgba(0, 0, 0, 00.3s);
    background: none;
    color: var(--text-color);
    outline: none;
    border-radius: 0.75rem;
    padding: 1.5rem;
    z-index: 1;

}

.contact_form-input:hover {
    color: red;
    border-color: blue;
}

.contact_form-tag {
    position: absolute;
    top: -0.75rem;
    left: 1.25rem;
    font-size: var(--smaller-font-size);
    padding: 0.25rem;
    background-color: var(--body-color);
    z-index: 10;
}

.contact_form-area {
    height: 11rem;
}

.contact_form-area textarea {
    resize: none;
}

.mail:hover {
    color: red;
  }

.whatsapp:hover {
color: green;
}

.messenger:hover {
    color:blue;
    }


    .contact-button{
        background: linear-gradient(90deg,#0162c8,#3327db);
    }

    .contact-button:hover {
        background: linear-gradient(90deg, #09550e, #1e6f2a);

    }

   
    
/* ==========break points========== */

@media screen and (max-width:992px) {
    .contact_container {
        column-gap: 3rem;
    }
  
  }
   
/* --------for medium devices----------- */

  @media screen and (max-width: 768px) {
    .contact_container {
        grid-template-columns: 1fr;
        row-gap: 3rem;
        width: 90%;
    }

    .contact_info {
        justify-content: center;
    }

    .contact_form {
        margin: 0 auto;
    }
  
  }

 
  @media screen and (max-width: 576px) {
    .contact_info {
        grid: template columns 1fr;
    }

    .contact_form {
        width: 100%;
    }
  
  }
  
  @media screen and (max-width: 350px) {
    
    .contact_info {
        grid: template columns 1fr;
    }

    .contact_form {
        width: 100%;
    }
  }
  